import React, { useEffect, useState, Fragment } from 'react';
import { PDFViewer } from '@react-pdf/renderer'
import Ledger from './components/ledger/cash/Ledger'

import { TransactionService } from '../../services/TransactionService';

import { CASH_MODEL } from '../../constants/models';

const CashLedger = ({}) => {

    const partyType = CASH_MODEL;

    const [ledger, setLedger] = useState(null);

    const transactionService = new TransactionService();

    useEffect(() => {
        transactionService.getLedgerByParty(partyType).then(data => {
            setLedger(data.rows);
            console.log(data.rows);
        });
    }, []);

    return (
        <div className="grid h-screen">
            <Fragment >
                {ledger!=null && (<PDFViewer className='card col-12' >
                    <Ledger data={ledger} />
                </PDFViewer>)}
            </Fragment>
        </div>
    )
};

export default CashLedger;